<template>
  <v-container>

    <!--     ""  "" -->
    <v-row>
      <v-col>
        <v-card>
          <v-card-title></v-card-title>
          <v-card-subtitle style="margin-top: 1vh;">{{ supervisor }}!</v-card-subtitle>

          <v-btn class="blue white--text" tile style="margin-top: 1vh; margin-bottom: 1vh; width: 100%">
            {{manageClass.toString()}}
          </v-btn>

          <!--          -->
          <v-col>
            <v-form>
              <v-container class="align-center">
                <v-row>
                  <v-col cols="24" sm="4" md="3">
                    <v-text-field
                        label=""
                        style="margin-left: 20%"
                        v-model="classNewNum"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="24" sm="3" md="2">
                    <v-btn color="success" style="margin-top: 2vh" @click="addNewClass"></v-btn>
                  </v-col>

                  <v-col cols="24" sm="6" md="2">
                    <v-text-field
                        label=""
                        v-model="classNumForRate"
                    ></v-text-field>
                  </v-col>

                  <v-col cols="24" sm="6" md="2">
                    <v-text-field
                        label=""
                        v-model="testRank"
                    ></v-text-field>
                  </v-col>

                  <v-col cols="24" sm="4" md="2">
                    <v-btn color="blue" class="white--text" style="margin-top: 2vh" @click="calRate"></v-btn>
                  </v-col>
                </v-row>
              </v-container>
            </v-form>
          </v-col>
        </v-card>
      </v-col>
    </v-row>


    <!--    -->
    <v-row>
      <v-col>
        <v-data-table
            :headers="header1"
            :items="stdData"
            sort-by="class"
            class="elevation-1"
        >
          <template v-slot:top>
            <v-toolbar flat>
              <!--              chooseTitle: .-->
              <v-toolbar-title>{{ choosedTitle }}</v-toolbar-title>
              <v-divider class="mx-4" inset vertical></v-divider>
              <v-spacer></v-spacer>
              <v-dialog  v-model="dialog" max-width="500px">
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                      color="success"
                      dark
                      class="mb-2"
                      style="margin-left: 1vh;margin-right: 1vh"
                      @click="getStudent"
                  >
                  </v-btn>
                  <v-btn
                      color="indigo"
                      dark
                      class="mb-2"
                      v-bind="attrs"
                      v-on="on"
                  >
                    
                  </v-btn>

                </template>
                <v-card>
                  <v-card-title>
                    <!--                    formTitle : /-->
                    <span class="text-h5">{{ formTitle }}</span>
                  </v-card-title>

                  <!--                  -->
                  <!--                   card-->
                  <v-card-text>
                    <v-container>
                      <v-row>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field v-model="editedItem.studentID" label=""></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field
                              v-model="editedItem.name"
                              label=""
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field
                              v-model="editedItem.classID"
                              label=""
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field
                              v-model="editedItem.score"
                              label=""
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field
                              cols="12"
                              sm="6"
                              md="4"
                              v-model="editedItem.passwd"
                              label=""
                          ></v-text-field>
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-card-text>

                  <!--                    card-->
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                        color="blue darken-1"
                        text
                        @click="close"
                    >
                      
                    </v-btn>
                    <v-btn
                        color="blue darken-1"
                        text
                        @click="save"
                    >
                      
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>

              <!--              -->
              <v-dialog v-model="dialogDelete" max-width="20%">
                <v-card>
                  <v-card-title class="text-h3 red--text text-center" style="margin-bottom: 1.5vh">
                  </v-card-title>
                  <v-divider></v-divider>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="red darken-1" text @click="deleteItemConfirm"></v-btn>
                    <v-btn color="blue darken-1" text @click="closeDelete"></v-btn>
                    <v-spacer></v-spacer>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-toolbar>
          </template>


          <!--            , , -->
          <template v-slot:item.actions="{ item }">
            <v-icon
                small
                class="mr-2"
                @click="editItem(item)"
            >
              mdi-pencil
            </v-icon>
            <v-icon
                small
                @click="deleteItem(item)"
            >
              mdi-delete
            </v-icon>
          </template>
          <template v-slot:no-data>
            <v-btn
                color="primary"
                @click="initialize"
            >
              Reset
            </v-btn>
          </template>

        </v-data-table>
      </v-col>
    </v-row>

    <!--      -->
    <v-row>
      <v-col>
        <!--        :headers-->
        <!--        :items-->
        <v-data-table
            :headers="header2"
            :items="schoolDetails"
            sort-by="class"
            class="elevation-1"
        >
          <template v-slot:top>
            <v-toolbar flat>
              <!--              chooseTitle: .-->
              <v-toolbar-title></v-toolbar-title>

              <v-divider class="mx-4" inset vertical></v-divider>
              <v-spacer></v-spacer>
              <!--              -->
              <!--              , -->
              <v-col cols="12" md="4">
                <v-combobox
                    v-model="collegeChoosen"
                    :items="getCollegeName(entireCollegeInformation)"
                    label=""
                    required
                    chips
                ></v-combobox>
              </v-col>

              <v-spacer></v-spacer>
              <!--              -->
              <v-btn
                  color="indigo"
                  dark
                  class="mb-2"
                  style="margin-left: 1vh;margin-right: 1vh"
                  @click="getStudentSituation"
              >
              </v-btn>
              <v-btn
                  color="success"
                  dark
                  class="mb-2"
                  style="margin-left: 1vh;margin-right: 1vh"
                  @click="getAllCollegeInformation"
              >
              </v-btn>
            </v-toolbar>
          </template>

        </v-data-table>

      </v-col>
    </v-row>

  </v-container>
</template>



<script>
import axios from "axios";

export default {
  name: 'Contacts',
  data: () => ({

    snack: true,
    //
    supervisor: '',
    stdGet: '',
    stdUpdate: '',
    stdDelete: '',
    stdRegister: '',

    //
    classNewNum:'',
    classNumForRate:'',
    testRank:'',
    manageClass:[],
    selectPage: '',
    choosedTitle: '',

    //<20 8<=<20
    dialog: false,
    dialogDelete: false,
    header1: [
      {
        text: '',
        align: 'start',
        sortable: false,
        value: 'studentID',
      },
      {text: '', value: 'name'},
      {text: '', value: 'classID'},
      {text: '', value: 'score'},
      {text: '', value: 'myRank'},
      {text: '', value: 'actions', sortable: false},
    ],
    stdData: [],
    editedIndex: -1,
    editedItem: {
      studentID: '',
      name: '',
      classID: '',
      score: '',
      passwd: '',
    },
    defaultItem: {
      studentID: '',
      passwd: '',
      name: '',
      score: '',
      classID: '',
    },
    test_url: "",//url
    test_var1: "",//var1
    test_var2: "",//var2

    //   
    header2:[
      {text: '', value: 'classid'},
      {text: '', value: 'studentid'},
      {text: '', value: 'name'},
    ],

    // , 
    collegeChoosen:[],

    schoolDetails: [],

    // 
    entireCollegeInformation:[],

  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? '' : ''
    },
  },

  watch: {
    dialog(val) {
      val || this.close()
    },
    dialogDelete(val) {
      val || this.closeDelete()
    },
  },

  created() {
    this.initialize()
  },

  mounted: function(){
    this.getAllCollegeInformation();
  },

  methods: {

    //  

    // 
    getStudentSituation:function(){
      var url = "http://localhost:9090/balance/getDetail";
      var that = this;
      //alert("");

      if(this.collegeChoosen[0] === undefined){
        alert("");
      }else{
        axios({
          url : url,
          method : 'post',
          params:{
            id: (this.entireCollegeInformation.find((item)=> item.name === this.collegeChoosen)).collegeID,
          }
        })
            .then(function(response) {

                if (response.data.code === "0") { // 
                  alert("");
                  //alert("id: "+ JSON.stringify(that.id));
                  that.schoolDetails = response.data.data;
                } else if (response.data.code === "1") { // 
                  alert("");
                  // alert("id: " + JSON.stringify(that.id));
                } else { //
                  alert(" ")
                }

            }).catch(function(){
          alert(" !!!!!!!!!")
        })
      }



    },

    // , 
    getAllCollegeInformation:function() {
      var that = this;
      var url = 'http://localhost:9090/college/all';
      axios.defaults.headers.common['token'] = localStorage.getItem("token");

      axios({
        url:url,
        method:'get',
        params:null,
      })
          .then(function(response){
            //alert("");
            that.entireCollegeInformation=response.data.data;
          })
          .catch(function(){
            alert("");
          })
    },

    // , 
    getCollegeName(ObjList){
      var list = new Array();
      for(var i = 0;i<ObjList.length;i++){
        list.push(ObjList[i]['name'])
      }
      return list;
    },

    // 

    addNewClass:function (){
      var that = this;
      axios({
        url: "http://127.0.0.1:9090/teacher/create_class",
        method: 'post',
        params:{
          id: that.classNewNum,
        }
      })
          .then(function (response) {
            alert("" + response.data.msg);
            that.manageClass.push(that.classNewNum);
          })
    },

    calRate:function (){
      var that = this;
      axios({
        url: "http://127.0.0.1:9090/teacher/get_1_Rate",
        method: 'post',
        params:{
          id: that.classNumForRate,
          rank1Score: that.testRank
        }
      })
          .then(function (response) {
            alert("" + response.data.data);
          })
    },

    // 
    //
    getStudent() {
      var that = this;
      var url = "http://127.0.0.1:9090/student/all";

      axios({
        url: url,
        method: 'get',
      })
          .then(function (response) {
            Object.assign(that.stdData, response.data.data);
            that.stdData.push();
          })
    },

    //
    updateStudent() {
      var that = this;
      var url = "http://127.0.0.1:9090/student/update";

      axios({
        url: url,
        method: 'post',
        params: {
          id: that.editedItem.studentID,
          name: that.editedItem.name,
          passwd: that.editedItem.passwd,
          classID: that.editedItem.classID,
          score: that.editedItem.score,
          rank: that.editedItem.myRank,
        }
      })
          .then(function (response) {
            alert("" + response.data.msg);
          })

    },

    // 
    deleteStudent(editedindex) {
      var that = this;
      var url = "http://127.0.0.1:9090/student/delete";

      axios({
        url: url,
        method: 'post',
        params: {
          id: that.stdData[editedindex]['studentID'],
        }
      })
          .then(function (response) {
            alert("" + response.data.msg);
          })
    },

    //
    registerStudent() {
      var that = this;
      var url = "http://127.0.0.1:9090/student/insert";

      axios({
        url: url,
        method: 'post',
        params: {
          id: that.editedItem.studentID,
          name: that.editedItem.name,
          passwd: that.editedItem.passwd,
          classID: that.editedItem.classID,
          score: that.editedItem.score,
        }
      })
          .then(function (response) {
            alert("" + response.data.msg);
          })
    },


    mounted(){
      this.getStudent();
    },


    initialize() {
      this.stdData = [
        ]
    },

    editItem(item) {
      this.editedIndex = this.stdData.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },

    deleteItem(item) {
      this.editedIndex = this.stdData.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true
    },

    deleteItemConfirm() {
      this.deleteStudent(this.editedIndex);
      this.stdData.splice(this.editedIndex, 1)
      this.closeDelete()
    },

    close() {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    closeDelete() {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    save() {
      if (this.editedIndex > -1) {
        Object.assign(this.stdData[this.editedIndex], this.editedItem);
      } else {
        this.stdData.push(this.editedItem)
      }
      if (this.editedIndex === -1) {
        this.registerStudent(this.editedIndex);
      } else {
        this.updateStudent(this.editedIndex);
      }
      this.close()
    },
  },
}
</script>
